.modal {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    background: rgb(0, 0, 0, 0.5);
}

.active {
    display: flex;
    justify-content: center;
    align-items: center;
}

.modalContent {
    padding: 25px;
    background: white;
    border-radius: 20px;
    max-width: 250px;
}